body {
  color: var(--main-dark-txt-cl);
  text-transform: uppercase;
  background-color: var(--main-light-bg);
  font-family: DM Sans, sans-serif;
  font-size: 14px;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  display: block;
}

img {
  max-width: 100%;
  height: auto;
  font-size: 0;
}

picture {
  font-size: 0;
  display: block;
  overflow: hidden;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  padding: 0;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

.section {
  padding-top: 40px;
  padding-bottom: 80px;
}

@media screen and (min-width: 768px) {
  .section {
    padding-bottom: 100px;
  }
}

@media screen and (min-width: 1440px) {
  .section {
    padding-top: 40px;
    padding-bottom: 72px;
  }
}

.section-title {
  letter-spacing: -.02em;
  font-size: 40px;
  font-weight: 500;
  line-height: 1;
}

@media screen and (min-width: 768px) {
  .section-title {
    font-size: 68px;
  }
}

.main-btn {
  height: 18px;
  letter-spacing: -.01em;
  text-transform: uppercase;
  color: var(--black-cl);
  border: 2px solid var(--btn-border-cl);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 40px;
  justify-content: center;
  align-items: center;
  padding: 14px 23px;
  font-family: DM Sans;
  font-weight: 700;
  line-height: 1.29;
  display: flex;
}

@media screen and (max-width: 374.98px) {
  .main-btn {
    font-size: 14px;
    line-height: 1.28;
  }
}

@media screen and (min-width: 768px) {
  .main-btn {
    height: 64px;
    padding: 20px 150px;
    font-size: 18px;
    line-height: 1.3;
  }
}

@media screen and (min-width: 1440px) {
  .main-btn {
    line-height: 1.333;
  }
}

.main-btn:hover, .main-btn:focus {
  color: var(--white-cl);
  background-color: var(--hover-light-cl);
}





.categories-list {
  height: 228px;
  width: 335px;
  flex-direction: column;
  gap: 24px;
  margin: 40px 0;
  padding-right: 28px;
  display: flex;
  overflow-y: scroll;
}

.categories-list::-webkit-scrollbar {
  width: 8px;
  border-radius: 12px;
}

.categories-list::-webkit-scrollbar-thumb {
  height: 114px;
  background: #e0e0e0;
  border-radius: 12px;
}

.categories-list li a {
  text-transform: capitalize;
  letter-spacing: -.02em;
  color: rgba(17, 17, 17, .6);
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  text-decoration: none;
  transition: color .3s ease-in;
}

.categories-list li a:hover {
  letter-spacing: -.02em;
  text-transform: uppercase;
  color: #4f2ee8;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
}

.book_temp {
  border-radius: 8px;
}

.category_title_last_word {
  letter-spacing: -.04em;
  color: #4f2ee8;
  font-family: DM Sans;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 38px;
}

.outlineli {
  outline: 1px solid red;
  display: inline-block;
}







:root {
  --brand-color: #4f2ee8;
  --white-cl: #fff;
  --black-cl: #111;
  --main-light-txt-cl: rgba(17, 17, 17, .6);
  --main-dark-txt-cl: rgba(255, 255, 255, .6);
  --modal-txt: rgba(17, 17, 17, .5);
  --modal-txt-dark: rgba(246, 246, 246, .5);
  --name-section-txt: #b4afaf;
  --blank-shopping-list: rgba(0, 0, 0, .6);
  --edit-sl-dark: rgba(255, 255, 255, .5);
  --btn-modal: #f3f3f3;
  --btn-modal-dark: #202024;
  --btn-switch-theme: linear-gradient(#fff, #f7f7f7);
  --scroll: #e0e0e0   --hover-dark-cl:#eac645;
  --main-light-bg: #f6f6f6;
  --main-dark-bg: #202024;
  --shopping-list: rgba(17, 17, 17, .4);
  --switch-light-theme: linear-gradient(#4f2ee8, #dcdcdc);
  --switch-dark-theme: linear-gradient(#4f2ee8, #686868);
  --gradient: radial-gradient(at top right, #4f2ee8, #fff);
  --z-index-header: 999;
  --z-index-hero-cont: 1;
  --z-index-rating-mark-block: 2;
}

